













import { Component, Prop, Vue } from 'vue-property-decorator'
import Big from 'big.js'
import { NumberFormatOptions } from 'vue-i18n'

@Component
export default class FormattedNumber extends Vue {
  @Prop({ required: true })
  value!: Big | number | string

  @Prop()
  currency?: string

  @Prop({ type: Boolean, default: false })
  percent!: boolean

  @Prop({ type: Boolean, default: false })
  focus!: boolean

  get valueAsNumber(): number {
    if (typeof this.value == 'number') return this.value
    else if (typeof this.value === 'string') return parseFloat(this.value)
    return this.value.toNumber()
  }

  get formatOptions(): NumberFormatOptions {
    if (this.currency) {
      return {
        style: 'currency',
        currency: this.currency,
        currencyDisplay: 'symbol',
      }
    } else if (this.percent) {
      return { style: 'percent' }
    } else {
      return { style: 'decimal' }
    }
  }
}
