
















import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class SelectCurrency extends Vue {
  @Prop({ default: '' })
  value!: string

  @Prop({ default: '' })
  label!: string

  @Prop({ default: '' })
  rules!: string

  update(event: string): void {
    this.$emit('input', event)
  }
}
